<template>
  <div>
    <div class="clearAllContainer">
        <span class="clearAllBtn" @click="clearTodo">
            Clear All
        </span>
    </div>
  </div>
    
  </template>
  
  <script>
  
  export default {
    methods:{
        clearTodo(){
            this.$emit('removeAll');
        }
    }
  }
  </script>
  
  <style scoped>
  .clearAllContainer {
    width: 8.5rem;
    height: 50px;
    line-height: 50px;
    background-color: white;
    border-radius: 5px;
    margin: 0 auto;
  }
  .clearAllBtn {
    color: #e20303;
    display: block;
    cursor: pointer;
  }
  .clearAllContainer:hover{
    background-color: #999;
  }

  .clearAllContainer:hover .clearAllBtn{
    color: white;
  }
</style>


