<template>
  <div id="app">
    <Banner :BannerState="BannerState" @bannerClose="closeBanner"></Banner>
    <Header></Header>
    <router-view />
  </div>
</template>

<script>
import Header from '@/components/Header'
import Banner from '@/components/common/Banner'
export default {
  name: 'App',
  data() {
    return{
      BannerState: true
    }
  },
  methods:{
    closeBanner(){
      this.BannerState = false;
    }
  },
  components: {
    Header,
    Banner
  }
}
</script>

<style>
* {
 margin: 0;
 padding: 0;
 box-sizing: border-box;
 font-family: 'Noto Sans KR', sans-serif;
 }
 html, body {
 font-size: 62.5%;
 }
 
 a {
 text-decoration: none;
 color: #2c3e50;
 font-size: 1.4rem;
 }

 .container {
 text-align: center;
 margin: 3rem 20px 0;
 }

 h2 {
 margin-bottom: 20px;
 font-weight: 700;
 font-size: 2.3rem;
 text-transform: uppercase;
 background-color: #d798fc;
 padding: 1rem 0;
 color: #fff
 }

 .postbox {
    margin: 20px 30px;
    font-size: 14px;
}

.f_wrap {
    display: flex;
    flex-wrap: wrap;
    background-color: rgb(241, 241, 241);
    padding: 10px 20px;
    text-align: left;
}

.title {
    flex-grow: 9;
    font-weight: 999;
}

.writer {
    flex-grow: 1;
}

.content {
    padding: 10px 20px;
    min-height: 10vw;
}
 
</style>



