<template>
    <section>
      <transition-group name="list" tag="ul">
        <li v-for="(todoItem, index) in propsdata" :key="index">
          <i class="checkBtn fas fa-check"></i>
          {{ todoItem }}
          <span class="removeBtn" type="button" @click="removeTodo(todoItem, index)">
            <i class="far fa-trash-alt"></i>
          </span>
        </li>
      </transition-group>
    </section>
</template>
  
<script>

export default {
  props: ['propsdata'],
  data() {
    return {
      todoItems: []
    };
  },
  methods: {
    removeTodo(todoItem, index) {
      this.$emit('removeTodo', todoItem, index);
    }
  },
  created() {
    if (localStorage.length > 0) {
      for (let i = 0; i < localStorage.length; i++) {
        this.todoItems.push(localStorage.key(i))
      }
    }
  }
}
</script>
  
<style scoped>
.shadow {
  box-shadow: 5px 10px 10px rgba(0, 0, 0, 0.03)
}

.list-enter-active,
.list-leave-active {
  transition: all 1s;
}

.list-enter,
.list-leave-to {
  opacity: 0;
  transform: translateY(30px);
}

section{
  margin: 20px 0 30px;
}

ul {
  list-style-type: none;
  padding-left: 0px;
  margin-top: 0;
  text-align: left;
}

li {
  display: flex;
  min-height: 50px;
  height: 50px;
  line-height: 50px;
  margin: 0.5rem 0;
  padding-left: 15px;
  background: white;
  border-radius: 5px;
}

.checkBtn {
  line-height: 45px;
  color: #62acde;
  margin-right: 20px;
}

.removeBtn {
  width: 50px;
  margin-left: auto;
  color: #de4343;
  text-align: center;
  border-radius: 0 5px 5px 0;
  cursor: pointer;
}

.removeBtn:hover {
  background-color: #9a9a9a;
  color: white;
}

.list-enter-active,
.list-leave-active {
  transition: all 1s;
}

.list-enter,
.list-leave-to {
  opacity: 0;
  transform: translateY(30px);
}</style>


