<template>
    <div class="container">
        <h2>사용자 게시판</h2>
        <div class="postbox" v-for="(value, index) in data" :key="index" @click="detail(index)">
            <div class="f_wrap">
                <div class="title">{{ value.title }}</div>
                <div class="writer">{{ value.writer }}</div>
            </div>
            <div class="content">{{ value.content }}</div>
        </div>
        <button @click="write">글쓰기</button>
    </div>
</template>
  
<script>
import data from './data';
export default {
    data() {
        return {
            data: data,
        };
    },
    methods: {
        detail(index) {
            this.$router.push({
                name: 'BDetail',
                params: {
                    contentId: index
                }
            });
        },
        write() {
            this.$router.push({
                path: '/board/create'
            })
        }
    }
}
</script>
  
<style scoped>
@import url("https://cdn.jsdelivr.net/gh/orioncactus/pretendard@v1.3.6/dist/web/static/pretendard.css");


button {
    border: 0;
    padding: 0 20px;
    line-height: 2;
    font-weight: 600;
    background-color: #2c3e50;
    color: #fff;
    margin: 10px 5px;
    display: inline-block;
    cursor: pointer;
}


.f_wrap {
    display: flex;
    flex-wrap: wrap;
    background-color: rgb(241, 241, 241);
    padding: 10px 20px;
    text-align: left;
}

.title {
    flex-grow: 9;
    font-weight: 999;
}

.writer {
    flex-grow: 1;
}

.content {
    padding: 10px 20px;
    background-color: aliceblue;
    min-height: 10vw;
}</style>
  


