<template>
    <div class="container">
        <h2>coffee list</h2>
        <div class="in_wrap">
            <div v-for="(c, i) in coffees" :key="i" class="content" @click="detail(i)">
                <h3>{{ c.title }}</h3>
                <img :src="c.image" alt="c.id">
            </div>
        </div>
        <button class="more" @click="more">더보기</button>
    </div>
</template>

<script>
import coffee from '@/assets/coffee';
import axios from 'axios';

export default {
    name: 'Product',
    data() {
        return {
            coffees: coffee,
            moreList: 0,
        };
    },
    methods: {
        detail(i) {
            this.$router.push({
                name: 'Detail',
                params: {
                    contentId: i
                }
            });
        },
        more() {
            axios.get(`https://joonsol.github.io/data/more0.json`)
                .then(result => {
                    console.log(result)
                    this.coffees.push(result.data);
                    this.moreList++
                    if (this.moreList == 2) {
                        this.moreList = 0
                    }
                })
        }
    }
}
</script>

<style>


 .in_wrap {
 justify-content: space-around;
 display: flex;
 flex-wrap: wrap;
 cursor: pointer;
 }
 img {
 width: 200px;
 height: auto;
 }
 h3 {
 color: gray;
 font-size: 1.6rem;
 margin: 3rem 0 1rem;
 position: relative
 }
 h3:after {
 display: block;
 content: "";
 width: 10%;
 height: .5rem;
 background-color: gainsboro;
 margin: 1rem auto
 }
.more {
    border: none;
    color: #fff;
    border-radius: 2rem;
    padding: 0 5rem;
    display: inline-block;
    margin-top: 3rem;
    line-height: 4rem;
    height: 4rem;
    cursor: pointer;
    background-color: rgb(107, 51, 160);
}
</style>


