import Vue from 'vue'
import Router from 'vue-router'
import Product from '@/components/tab1/Product'
import Detail from '@/components/tab1/Detail'
import Todo from '@/components/tab2/Todo'
import Weather from '@/components/tab3/Weather'
import Board from '@/components/tab4/Board'
import BCreate from '@/components/tab4/BCreate'
import BDetail from '@/components/tab4/BDetail'



Vue.use(Router)


export default new Router({
  routes: [
    {
      path: '/',
      name: 'Product',
      component: Product
    },
    {
      path: '/detail/:contentId',
      name: 'Detail',
      component: Detail
    },
    {
      path: '/todo',
      name: 'Todo',
      component: Todo
    },
    {
      path: '/weather',
      name: 'Weather',
      component: Weather
    },
    {
      path: '/board',
      name: 'Board',
      component: Board
    },
    {
      path: '/board/create',
      name: 'BCreate',
      component: BCreate
    },
    {
      path: '/board/detail/:contentId',
      name: 'BDetail',
      component: BDetail
    },
    
    
    
  ]
})



