<template>
    <div class="container">
        
        <h2><span class="Bback" @click="back">&lt; 이전</span>상세페이지</h2>
        <div class="postbox">
            <div class="f_wrap">
                <div class="title">{{ data.title }}</div>
                <div class="writer">{{ data.writer }}</div>
            </div>
            <div class="content">
                <img :src="data.image" alt="">
                <div class="contentTxt">
                    {{ data.content }}
                </div>
                
            </div>
        </div>
        <button @click="updateData">수정</button>
        <button @click="deleteData">삭제</button>
    </div>
</template>

<script>
import data from './data';
export default {
    name: 'BDetail',
    data(){
        const index = this.$route.params.contentId
        return{
            data: data[index],
            index: index,
        }
    },
    methods:{
        updateData(){
            this.$router.push({
                name: 'BCreate',
                params:{
                    contentId: this.index
                }
            });
        },
        deleteData(){
            data.splice(this.index, 1);
            this.$router.push({
                path: '/board'
            });
        },
        back(){
            this.$router.push({
                path: '/board'
            });
        }
    }
}
</script>

<style scoped>
h2{
    position: relative;
}
h2>.Bback{
    position: absolute;
    left: 10px;
    font-size: 16px;
    top: 0;
    transform: translate(0, 70%);
    cursor: pointer;
}
.Bback:hover{
    color: #333;
}
button {
    border: 0;
    padding: 0 20px;
    line-height: 2;
    font-weight: 600;
    background-color: #2c3e50;
    color: #fff;
    margin: 10px 5px;
    display: inline-block;
    cursor: pointer;
}
img {
    width: 300px;
    margin-bottom: 20px;
}


</style>


