<template>
  <div id="Todo" class="container">
    <todoHeader></todoHeader>
    <div class="contbox">
      <todoInput @addTodo="addTodo"></todoInput>
      <todoList :propsdata="todoItems" @removeTodo="removeTodo"></todoList>
      <todoFooter @removeAll="clearAll"></todoFooter>
    </div>

  </div>
</template>
  
<script>
import todoHeader from '@/components/tab2/list/todoHeader.vue'
import todoInput from '@/components/tab2/list/todoInput.vue'
import todoList from '@/components/tab2/list/todoList.vue'
import todoFooter from '@/components/tab2/list/todoFooter.vue'

export default {
  name: 'Todo',
  data() {
    return {
      todoItems: [],
    };
  },
  components: {
    todoHeader,
    todoInput,
    todoList,
    todoFooter,
  },
  methods: {
    addTodo(todoItem) {
      localStorage.setItem(todoItem, todoItem);
      this.todoItems.push(todoItem);
    },
    removeTodo(todoItem, index) {
      localStorage.removeItem(todoItem);
      this.todoItems.splice(index, 1);
    },
    clearAll() {
      localStorage.clear();
      this.todoItems = [];
    }
  },
  created() {
    if (localStorage.length > 0) {
      for (let i = 0; i < localStorage.length; i++) {
        if (localStorage.key(i) !== 'loglevel:webpack-dev-server'){
          this.todoItems.push(localStorage.key(i));
        }
      }
    }
  }
}
</script>
  
<style scoped>
input {
  border-style: groove;
  width: 200px;
  font-size: 14px;
}

button {
  border-style: groove;
}

#Todo {
  
  font-size: 14px;
  
}

.contbox{
  background-color: #eee;
  padding: 30px 20px;
}

.shadow {
  box-shadow: 5px 10px 10px rgba(0, 0, 0, 0.03)
}
</style>
  


