<template>
    <div class="container">
        <h2>{{ index !== undefined ? '수정하기' : '글쓰기' }}</h2>
        <div class="container">
            <input type="text" v-model="writer" placeholder="글쓴이">
            <input type="text" v-model="title" placeholder="제목">
            <input type="text" v-model="image" placeholder="이미지링크">
            <textarea v-model="content" cols="30" rows="10" placeholder="내용을 입력해 주세요"></textarea>
        </div>
        <button @click="index !== undefined ? update() : write()">
            {{ index !== undefined ? '수정하기' : '글쓰기' }}
        </button>
    </div>
</template>
<script>
import data from "./data";

export default {
    name:'BCreate',
    data(){
        const index = this.$route.params.contentId;
        return{
            data:data,
            index: index,
            writer: index !== undefined ? data[index].writer : '',
            title: index !== undefined ? data[index].title : '',
            content: index !== undefined ? data[index].content : '',
            image: index !== undefined ? data[index].image : ''
        }
    },
    methods:{
        update(){
            data[this.index].writer = this.writer;
            data[this.index].title = this.title;
            data[this.index].content = this.content;
            this.$router.push({
                path: "/board"
            });
        },
        write(){
            this.data.push({
                writer: this.writer,
                title: this.title,
                content: this.content
            });
            this.$router.push({
               path: "/board", 
            });
        }
    }
}
</script>
<style scoped>

button {
    border: 0;
    padding: 0 20px;
    line-height: 2;
    font-weight: 600;
    background-color: #2c3e50;
    color: #fff;
    margin: 10px 5px;
    display: inline-block;
    cursor: pointer;
}
input,
textarea {
    border: 0;
    width: 100%;
    display: block;
    background-color: #f4faff;
    line-height: 2;
    text-indent: 20px;
    margin-bottom: 10px
}
</style>


